import { axiosClient } from "@rp/react/axios-client"
import { FILES_BASE_URL, FILES_ROOT } from "../file.constants"
import { FileDeleteManyResponse } from "../file.types"

const deleteMany = async (files: Array<string>) => {
  const { data } = await axiosClient.patch<FileDeleteManyResponse>(
    `${FILES_BASE_URL}${FILES_ROOT}`,
    {
      action: "DELETE",
      files,
    },
  )
  return data
}

export { deleteMany }
